body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Droid Sans', 'Helvetica Neue', 'Sans-serif';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image:url(bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
  display: block;
  width: 350px;
  padding: 10px;
  margin: auto;
  margin-bottom: 15px;
  color: white; 
  border: none;
  border-radius: 3px;
}
img {
  max-width: 400px;
  max-height: 400px;
  margin: auto;
  margin-top: 20px;
  display: block;
  box-shadow: 0px 0px 10px black;
}
input {
  box-sizing: border-box;
  display: block;
  padding: 10px;
  width: 350px;
  margin: auto;
  margin-bottom: 15px;
}
h1 {
  display: block;
  text-align: center;
  font-size: 50px;
  text-shadow: 0px 0px 5px rgb(228, 240, 231);
  color: #5D2E46;
} 

h2 {
  display: block;
  margin-top: auto;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-align: center;
  font-size: 20px;
  text-shadow: 0px 0px 5px rgb(228, 240, 231);
  color: black;
} 
h3 {
  display: block;
  position: fixed;
  bottom: 5px;
  left: 5px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-align: left;
  font-size: 20px;
  color: black;
} 